import { Component, OnInit } from '@angular/core';
import { ModuleConfig } from '../../../module-config';
import { FormControl, FormGroup, FormArray, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { GuildService } from '../../../services/guild.service';

@Component({
  selector: 'app-announce-module',
  templateUrl: './logs-module.component.html',
  styleUrls: ['./logs-module.component.css']
})
export class LogsModuleComponent extends ModuleConfig implements OnInit {
  EventType = EventType;

  moduleName = 'logs';

  events = [
    EventType.Ban,
    EventType.LevelUp,
    EventType.MemberJoin,
    EventType.MemberLeave,
    EventType.MessageDeleted,
    EventType.Unban,
    EventType.Warn
  ];

  eventConfigs: AnnounceEvent[] = [];

  constructor(
    guildService: GuildService,
    route: ActivatedRoute,
    saveChanges: MatSnackBar) {
    super(guildService, route, saveChanges);
  }

  async ngOnInit() {
    await super.init();

    this.eventConfigs = this.savedGuild.logs.events;
  }

  buildForm({ logs }: any) {
    const formGroup = new FormGroup({
      events: new FormArray([])
    });
    for (let i = 0; i < this.events.length; i++) {
      const event = this.events[i];
      const config = logs.events.find(e => e.event === event); 

      (formGroup.get('events') as FormArray).push(new FormGroup({
        event: new FormControl(event),
        enabled: new FormControl(config?.enabled ?? false),
        channel: new FormControl(config?.channel ?? ''),
        message: new FormControl(config?.message ?? this.LogsMessage(event), Validators.maxLength(512)),
        embed: new FormGroup({
          title: new FormControl(config?.embed?.title ?? ''),
          description: new FormControl(config?.embed?.description ?? ''),
          image: new FormControl(config?.embed?.image ?? ''),
          color: new FormControl(config?.embed?.color ?? ''),
          thumbnail: new FormControl(config?.embed?.thumbnail ?? ''),
          author: new FormGroup({
            text: new FormControl(config?.embed?.author?.text ?? ''),
            image: new FormControl(config?.embed?.author?.image ?? '')
          }),
          footer: new FormGroup({
            text: new FormControl(config?.embed?.footer?.text ?? ''),
            image: new FormControl(config?.embed?.footer?.image ?? ''),
            timestamp: new FormControl(config?.embed?.footer?.timestamp ?? true)
          })
        })
      }));     
    }
    return formGroup;
  }


  LogsMessage(event: EventType) {

    if(event == EventType.MemberJoin) return `[USER] has joined!`;
    if(event == EventType.MemberLeave) return `[USER] has left!`;

    if(event == EventType.MessageDeleted) return `[USER] deleted a message in \`[CHANNEL]\` with content: \`[MESSAGE]\``;

    if(event == EventType.Ban) return '[STAFF] `BANNED` [USER] for [REASON]'
    if(event == EventType.Unban) return '[STAFF] `UNBANNED` [USER] for [REASON]'

    if(event == EventType.LevelUp) return '[USER] has leveled up to level [NEW_LEVEL]!'
    if(event == EventType.Warn) return '[STAFF] `WARNED` [USER] for [REASON]'
    
    
    else return `\`${event}\` was triggered in **[GUILD]**!`;
  }

  filterFormEvents(value: any) {
    value.events = value.events.filter(e => e?.enabled);
  }

  getEvent(eventType: EventType) {
    return this.eventConfigs.find(e => e.event === eventType);
  }

  async submit() {
    this.filterFormEvents(this.form.value);  
    await super.submit();
  }
}

export enum EventType {
  Ban = 'BAN', 
  LevelUp = 'LEVEL_UP',
  MessageDeleted = 'MESSAGE_DELETED',
  MemberJoin = 'MEMBER_JOIN',
  MemberLeave = 'MEMBER_LEAVE',
  Unban = 'UNBAN', 
  Warn ='WARN'
}

export interface AnnounceEvent {
  event: EventType;
  channel: string;
  message: string;
  embed: Embed;
}

export interface Embed {
  messageContent: string;
  title: string;
  image: string;
  description: string;
  /* url: string; Not needed as of right now */
  color: string;
  thumbnail: string;
  author: {
    image: string;
    text: string;
  }
  footer: {
    text: string;
    image: string;
    timestamp: boolean;
  }
}