import { Component, OnInit } from '@angular/core';
import { FormGroup, FormArray, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { GuildService } from '../../../services/guild.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ModuleConfig } from '../../../module-config';

@Component({
  selector: 'embed-compose-module',
  templateUrl: './compose-embed.component.html',
  styleUrls: ['./compose-embed.component.css'],
})
export class EmbedComposeModuleComponent extends ModuleConfig implements OnInit {
  levelRoleIndices = [0,1,2,3,4,5,6,7];
  moduleName = 'Embed Compose';
  color: string = '#4f545c'

  disabled = true;
  canSend = false;


  hidden = {
    messageContent: true,
    title: true,
    url: true,
    image: true,
    color: true,
    description: true,
    footer: {
      text: true,
      image: true
    },
    author: {
      image: true,
      url: true,
      text: true
    }
  }


  constructor(
    guildService: GuildService,
    route: ActivatedRoute,
    saveChanges: MatSnackBar) {
    super(guildService, route, saveChanges);
  }

  async ngOnInit() {
    await super.init();
    console.log(this.routeName)   
    
    this.form.valueChanges.subscribe(() => {
      if(this.title || this.description) this.disabled = false;
      else this.disabled = true;

      let canSend = this.title || this.message || this.description

      if(canSend && this.channel) this.canSend = true;
      else this.canSend = false;
    });
  }
  
  buildForm() {
    const formGroup = new FormGroup({
      message: new FormControl('', [
        Validators.maxLength(256) 
      ]),
      channel: new FormControl(''),
      title: new FormControl('', [ Validators.maxLength(126) ]),
      description: new FormControl('', [ Validators.maxLength(256) ]),
      footer: new FormControl('', [ Validators.maxLength(26) ]),
      footerImage: new FormControl('', [ Validators.maxLength(256) ]),
      authorImage: new FormControl('', [ Validators.maxLength(256) ]),
      authorName: new FormControl('', [ Validators.maxLength(256) ]),
      authorUrl: new FormControl('', [ Validators.maxLength(256) ]),
      embedImage: new FormControl('', [ Validators.maxLength(256) ]),
      url: new FormControl('', [ Validators.maxLength(256) ]),
    });
    return formGroup;
  }

  async send() {
    if(!this.canSend) return;
    console.log('send embed on discord.', this.dataToBeSent())
    const channelId = this.form.get('channel').value;
    await this.guildService.sendEmbed(this.guildId, channelId, this.dataToBeSent()).then((message) => {
      alert(message.message)
      console.log('embed sent.')
    }).catch(err => {
      console.log('error while sending embed.', err)
    });
  }

  //get message value from form group
  get message() { return this.form.get('message').value; }
  get title() { return this.form.get('title').value; }
  get description() { return this.form.get('description').value; }
  get footerText() { return this.form.get('footer').value; }
  get footerImage() { return this.form.get('footerImage').value; }
  get authorImage() { return this.form.get('authorImage').value; }
  get authorName() { return this.form.get('authorName').value; }
  get authorUrl() { return this.form.get('authorUrl').value; }
  get image() { return this.form.get('embedImage').value; }
  get url() { return this.form.get('url').value; }
  get channel() { return this.form.get('channel').value; }

  dataToBeSent() {
    return {
      message: this.message,
      title: this.title,
      description: this.description,
      footer: {
        text: this.footerText,
        icon: this.footerImage,

      },
      author: {
        name: this.authorName,
        url: this.authorUrl,
        icon: this.authorImage,
      },
      image: this.image,
      color: this.color,
      url: this.url,
    }
  }

  get author() {
    return {
      name: this.authorName,
      url: this.authorUrl,
      icon: this.authorImage,
    }
  }
}