import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ModuleConfig } from '../../../module-config';
import { GuildService } from '../../../services/guild.service';

@Component({
  selector: 'captcha-module',
  templateUrl: './captcha-module.component.html',
  styleUrls: ['./captcha-module.component.css']
})
export class CaptchaModuleComponent extends ModuleConfig implements OnInit {
  moduleName = 'captchaSettings';


  get general() { return this.savedGuild.general; }
  get captchaSettings(): captcha { return this.savedGuild.captchaSettings; }

  constructor(
    guildService: GuildService,
    route: ActivatedRoute,
    saveChanges: MatSnackBar) {
    super(guildService, route, saveChanges);
  }

  async ngOnInit() {
    await super.init();


    
  }

  buildForm({ general, captchaSettings }: any) {
    const formGroup = new FormGroup({
      captchaChannel: new FormControl(captchaSettings.captchaChannel ?? ''),
      unverifiedRole: new FormControl(captchaSettings.unverifiedRole ?? ''),
      verifiedRole: new FormControl(captchaSettings.verifiedRole ?? '')
    });
    return formGroup;
  }
}

export interface captcha {
  captchaVerification: boolean;
  captchaChannel: string;
  unverifiedRole: string;
  verifiedRole: string;
}
