import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/services/api.service';
import { Router } from '@angular/router';
import ms from 'ms'

import { GuildService } from 'src/app/services/guild.service';
import { UserService } from '../../../services/user.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard-overview.component.html',
  styleUrls: ['./dashboard-overview.component.css']
})
export class DashboardOverviewComponent implements OnInit {
  get user() { return this.userService.user; }
  get reps() { return this.userService.savedUser.xpCard.reps }
  get userTitle() { return this.userService.savedUser.xpCard.title }
  StatsShown: boolean = true;
  members: any;
  balance: any;
  savedMember: any;
  member: any;
  hasPerms: boolean = false;
  guild: any = {};
  reminders: Reminder[];

  constructor(
    public apiService: ApiService,
    private router: Router,
    private guildService: GuildService,
    private userService: UserService) {
    document.title = 'Neko - Dashboard';
  }

  isUserTitle() {
    if (this.userTitle) {
      return true;
    }
    return false;
  }

  isItMorning() {
    let hour = new Date().getHours();
    if (hour < 12) {
      return true;
    }
    return false;
  }

  adminDashboard() {
    this.router.navigate([`/dashboard/admin/${this.guild.id}`]);
  }

  async ngOnInit(): Promise<void> {
    const guildId = environment.guildId;

    this.members = await this.guildService.getMembers(guildId);

    this.members.forEach(async (member) => {
      if(member.id == this.user.id) {
        this.member = member;
      this.balance = await this.guildService.getBalance(guildId, this.user.id);

      }
    })

    let savedmember = await this.guildService.getSavedMember(guildId, this.user.id);
    this.savedMember = savedmember;

    let userHasPerms = await this.guildService.getMemberStatus(guildId, this.user.id);
    this.hasPerms = userHasPerms;

    let userReminders: Reminder[] = await this.userService.getReminders();
    this.reminders = userReminders;

    this.guild = await this.guildService.getPublicGuild(guildId);
  }

  getReminderExpiery(reminder: Reminder) {
    return ms(reminder.expiresMilliseconds - Date.now())
  }

}

export interface Reminder {
  id: string;
  content: string;
  expires: Date;
  expiresMilliseconds: number;
}
